import React from 'react';
import axios from "axios";
import Movie from "./Movie";
import "./App.css";

/*
  - axios 
   -> facth 위에 작은 Layer라고 볼 수 있음 (facth랑 비슷한 역할을 함)
  
   -> TERMINAL 명령어 : npm i axios 로 다운 가능

*/
class App extends React.Component {

  state = {
    isLoding: true,
    movies: []
  };

  /*
    - axios가 실행되기까지는 약간의 시간이 소요 되기 때문에 다음과 같이 처리
     -> async : 비동기를 의미
     
     -> await : await 뒤에 작업이 실행 될 때까지 기다렸다가 실행 된 후 
               나머지 작업 진행
  */
  getMovies = async () => {
    const {
      data: {
        data: { movies }
      }
    } = await axios.get("https://yts-proxy.now.sh/list_movies.json?sort_by=rating");
    this.setState({ movies, isLoding: false });
  }

  componentDidMount() {
    this.getMovies();
  }
  render() {
    const { isLoding, movies } = this.state;
    return (
      <section className="container">
        {isLoding ? (
          <div className="loader">
            <span className = "loader__text">Loading...</span>
          </div>
          ) : (
            <div className="movies">
               {movies.map(movie => (
              <Movie
                key={movie.id}
                id={movie.id}
                year={movie.year}
                title={movie.title}
                summary={movie.summary}
                poster={movie.medium_cover_image}
                genres={movie.genres}
              />
            ))
  }
            </div>
          )}
      </section>
    );
  }
}
export default App;
